ight: 1rem;*/\n  float: left;\n  font-size: .25rem;\n  color: #7b7b7b;\n}\n.pr-tab-wrap .list-wrap li span {\n  display: block;\n  width: 4.5rem;\n  float: left;\n  line-height: 150%;\n  font-size: .25rem;\n  color: #333333;\n}\n.pr-tab-wrap .list-wrap .list-com {\n  height: 1rem;\n  font-size: .24rem;\n  color: #333;\n  display: -webkit-box;\n  overflow: hidden;\n  white-space: normal!important;\n  text-overflow: ellipsis;\n  word-wrap: break-word;\n  -webkit-line-clamp: 3;\n  -webkit-box-orient: vertical;\n}\n.pr-tab-wrap .comment-wrap ul {\n  height: 1.05rem;\n  display: flex;\n}\n.pr-tab-wrap .comment-wrap ul li {\n  width: 20%;\n  height: 1rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n}\n.pr-tab-wrap .comment-wrap ul li span {\n  color: #B9B9B9;\n  font-size: 0.32rem;\n  margin-bottom: 0.1rem;\n}\n.pr-tab-wrap .comment-wrap ul li p {\n  font-size: 0.28rem;\n}\n.pr-tab-wrap .comment-wrap ul .conActive {\n  border-bottom: 0.05rem solid #d02629;\n}\n.pr-tab-wrap .comment-wrap h2 {\n  padding: 0 .2rem;\n  height: .88rem;\n  font-size: .32rem;\n  line-height: .88rem;\n  background: none;\n  color: #333;\n}\n.pr-tab-wrap .comment-wrap h2 span {\n  font-size: .32rem;\n  color: #f9781e;\n}\n.pr-tab-wrap .comment-wrap .list-wrap {\n  width: 7.1rem;\n  padding: 0 .2rem;\n  height: 3.5rem;\n  background: #fff;\n  margin-bottom: .1rem;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-hd {\n  padding: .2rem 0;\n  line-height: .32rem;\n  height: .3rem;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-hd .pull-left {\n  padding-left: .46rem;\n  font-size: .2rem;\n  color: #b0b0b0;\n  position: relative;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-hd .pull-left img {\n  width: .32rem;\n  height: .32rem;\n  position: absolute;\n  left: 0;\n  top: 0;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-hd .pull-right {\n  color: #999;\n  font-size: .26rem;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-hd .pull-right img {\n  width: 0.3rem;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-img-main {\n  height: 1rem;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-img-main img {\n  width: 1rem;\n  height: 1rem;\n  margin-right: .2rem;\n  transform: scale(1);\n  /*图片原始大小1倍*/\n  transition: all ease 0.5s;\n  /*图片放大所用时间*/\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-img-main img.active {\n  transform: scale(3);\n  /*图片需要放大3倍*/\n  position: absolute;\n  /*是相对于前面的容器定位的，此处要放大的图片，不能使用position：relative；以及float，否则会导致z-index无效*/\n  z-index: 100;\n}\n.pr-tab-wrap .comment-wrap .list-wrap .list-purTimer {\n  padding-top: .2rem;\n  font-size: .2rem;\n  color: #999;\n}\n.pr-tab-wrap .list-img-wrap {\n  background: #fff;\n  margin-top: .2rem;\n}\n.pr-tab-wrap .list-img-wrap img {\n  width: 100%;\n}\n"],"sourceRoot":""}]);

